
.dots{transform-style:preserve-3d;perspective:5px
}
.dot:after{content:"";position:absolute;top:-50%;left:-50%;width:200%;height:200%;background:#fff;border:4px solid #111;border-radius:50%;transform:scale(0.5);transition:all 0.6s cubic-bezier(0.85, 0.01, 0.13, 1.02);will-change:transform
}



